.App {
  text-align: left;
  padding: 1em;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

table {
    border-spacing: 0;
    border: 1px solid black;
}

tr:last-child td {
    border-bottom: 0;
}

th,
td {
      margin: 0;
      padding: 0.5rem;
      border-bottom: 1px solid black;
      border-right: 1px solid black;
}

th:last-child,
td:last-child {
    border-right: 0;
}

table th {
  vertical-align: top;
  white-space: nowrap;
}
table tr {
  text-align: left;
  white-space: nowrap;
}
